  /**-------------------------------------------------**
   * @desc - Manage sections                       -----*
   *----------------------------------------------------*/

  function addToCurrentSection(pElement, pChildren) {
    // console.log(`--->: newElement: addToCurrentSection -> pElement, pChildren`, pElement, pChildren)
    let newElement;
    if (pElement === 'Matrix') newElement = getNewElement(pElement, true);
    else if (pElement === 'Matrix.Button') newElement = getNewElement(pElement, false, true);
    else if (pElement === 'Repeatable') newElement = getNewElement(pElement, false, false, true);
    else if (pElement === 'SignatureSignOnOff') newElement = getNewElement(pElement, false, false, false, pChildren);
    else if (pElement === 'QRScanner.List') newElement = getNewElement(pElement, false, false, false, pChildren);
    else newElement = getNewElement(pElement);

    // Special properties
    if (pElement === 'Trilean.Label') {
      initButtonsProperty(newElement);
      initDefaultValues(newElement);
    }
    if (pElement === 'Text') initFormatProperty(newElement);

    // Add to section
    if (json.Sections[currentSectionIndex] !== undefined) {
      let currentElements = json.Sections[currentSectionIndex].Elements;
      currentElements.push(newElement);
      setJson({ Sections: [...json.Sections] });
    } else {
      let currentSection;
      currentSection = getNewSection();
      currentSection.Elements.push(newElement);
      setJson({ Sections: [...json.Sections, currentSection] });
      if (currentSectionIndex > 0) setCurrentSectionIndex(currentSectionIndex + 1);
    }
  }

  function addNewSection() {
    setJson({ Sections: [...json.Sections, getNewSection()] });
    if (json.Sections.length > 0) setCurrentSectionIndex(json.Sections.length);
  }

  function addToNewSection(pElement, pChildren) {
    let newSection = getNewSection(pElement);

    pChildren.forEach((child) => newSection.Elements.push(child));

    setJson({ Sections: [...json.Sections, newSection] });
    if (json.Sections.length > 0) setCurrentSectionIndex(json.Sections.length);
  }

