import React, { useState, useEffect } from 'react';

export default function SlidersGroup(props) {
  const { ratios, updateRatios } = props;
  const [sliders, setSliders] = useState([]);

  function initSlidersState() {
    setSliders(
      ratios.map((value, index) => {
        return { index, value, locked: false };
      })
    );
  }
  function updateSlidersState() {
    setSliders(
      sliders.map((value, index) => {
        return { index, value: ratios[index], locked: value.locked };
      })
    );
  }

  function changeRatioHandler(pEvent) {
    const currentSliderIndex = !pEvent.target.id.includes('slider') ? Number(pEvent.target.id.replace('ratio', '')) : Number(pEvent.target.id.replace('slider_ratio', ''));
    const currentSliderValue = Number(pEvent.target.value);

    const offset = currentSliderValue - ratios[currentSliderIndex];
    const updatedRatios = [...ratios];

    const openSlidersList = sliders.reduce((acc, curr, index) => {
      if (curr.locked === false && curr.index !== currentSliderIndex) acc.push(curr);
      return acc;
    }, []);

    let isAdjustable;
    const prevSliderValue = ratios[currentSliderIndex];
    const delta = 100 - prevSliderValue - sliders.filter((slider) => slider.locked === true).reduce((acc, current, index) => acc + current.value, 0);

    if (offset > 0) isAdjustable = openSlidersList.some((item) => item.value > 0);
    else if (offset < 0) isAdjustable = openSlidersList.some((item) => item.value < 100);

    if (!sliders[currentSliderIndex].locked && isAdjustable) {
      let deltaToSpread = Math.min(offset, delta);
      for (let index = 0; index < openSlidersList.length; index++) {
        const slider = openSlidersList[index];
        if (offset < 0 && slider.value === 100) continue;
        if (offset > 0 && slider.value === 0) continue;
        if (slider.value >= 0 && slider.value <= 100) {
          let prevSliderValue = updatedRatios[slider.index];
          updatedRatios[slider.index] -= deltaToSpread;
          deltaToSpread = deltaToSpread - prevSliderValue;
          if (updatedRatios[slider.index] < 0) updatedRatios[slider.index] = 0;
          if (updatedRatios[slider.index] > 100) updatedRatios[slider.index] = 100;

          if (deltaToSpread > 0) continue;
          else break;
        }
      }

      updatedRatios[currentSliderIndex] = Math.min(pEvent.target.value, prevSliderValue + delta);
      updateRatios(updatedRatios);
    }
  }

  function lockHandler(pEvent) {
    const lockIndex = Number(pEvent.target.id.replace('lock', ''));

    const slidersUpdate = [...sliders];
    slidersUpdate[lockIndex].locked = !slidersUpdate[lockIndex].locked;
    setSliders(slidersUpdate);
  }

  useEffect(() => {
    if (ratios.length !== sliders.length) initSlidersState();
    else updateSlidersState();
  }, [ratios]);

  return (
    <div>
      {sliders.map((item, index) => {
        return (
          <div key={index}>
            <div>
              <input type="text" value={item.value} id={`ratio${index}`} onChange={changeRatioHandler} size="3" />
            </div>
            <div style={{ width: '100%', display: 'flex', alignItems: 'center' }}>
              <input type="range" value={item.value} name="" id={`slider_ratio${index}`} style={{ width: '100%' }} onChange={changeRatioHandler} />
              <input type="checkbox" name="" id={`lock${index}`} checked={item.locked} onChange={lockHandler} />
            </div>
          </div>
        );
      })}
    </div>
  );
}
