import React from "react";

import styles from "./Popin.module.scss";

import { useTransition, animated } from "react-spring";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExclamationTriangle, faTimes } from "@fortawesome/free-solid-svg-icons";

const Popin = props => {
  const transitions = useTransition(props.visibility, null, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 },
  });

  return transitions.map(
    ({ item, key, props: transitionProps }) =>
      item && (
        <animated.div key={key} style={transitionProps}>
          <div className={styles.backdrop}>
            <div className={styles.container}>
              <div className={styles.header}>
                <div>Edit the text</div>
                <div className={styles.closeBtn} onClick={props.onClose}>
                  <FontAwesomeIcon icon={faTimes} />
                </div>
              </div>
              <div className={styles.content}>{props.children}</div>
            </div>
          </div>
        </animated.div>
      )
  );
};

export default Popin;
